import React from "react";
function Navbar (){
    const resumeURL = "path/to/resume.pdf";
    return(
        <div id="nav-menu">
        <h3><a href='#home' class="nav-link home">Home</a></h3>
        <h3><a href='#about'class="nav-link about">About</a></h3>
        <h3><a href='#skills'class="nav-link skills">Skills</a></h3>
        <h3><a href='#projects' class="nav-link projects">Projects</a></h3>
        <h3><a href='#contact' class="nav-link contact">Contact</a></h3>
        <h3><a href={resumeURL} id="resume-link-1" class="nav-link resume" target="_blank" download>Resume</a></h3>
        {/* <h3><a href='#'class="nav-link resume">Resume</a></h3> */}
        </div>

    )
}
export default Navbar;
