import React, { useState, useEffect } from 'react';
function App() {
  const [matches, setMatches] = useState(window.matchMedia("(min-width: 768px)").matches);
  useEffect(() => {
  const handler = e => setMatches(e.matches);
  window.matchMedia("(min-width: 768px)").addListener(handler);
  return () => {
  window.matchMedia("(min-width: 768px)").removeListener(handler);
  };
  }, []);
  return (
    <div style={{marginBottom:"1%"}}>
      {matches && <h1></h1>}
      {!matches && <h3></h3>}
        <Home/>
      <div className="App" style={{position:"relative",zIndex:"1",background:"#94f0f1"}}>
      <div style={{width:"35%",textAlign:"left"}}>
      <Name/>
      <FormalIntro/>
       <br/>
       <Resume/>
      <br/>
      <div className="profile1">
        <Github/>
       <Linkdin/>
       <EmailAddress/>
       <Facebook/>
       <Instagram/>
       <Whatsapp/>
       <Twitter/>
       </div>
     </div>
     <div className="Navabar1">
     <Image/>
     <div className='lineHeight1'>
     <Navbar/>
     </div>
     </div>
     </div>
     <br/>
    <div>
    <br/><br/><br/><br/>
     <About/>
     <br/><br/><br/><br/>
     <Skills/>
     <br/><br/><br/><br/><br/><br/><br/><br/>
     <Projects/>
    </div>
      <Portfolio/>
       <Statics/>
       <GitStats/>
        <br/><br/>
        <GitHubCalendarHeatmap/>
       <Sample/>
       {/* <DarkMode></DarkMode> */}
    </div>
  );
}
export default App;


